<template>
	<div id="recommend" class="animated fadeInLeft">
		<el-container>
			<el-header></el-header>
			<div class="sub-title">
				<el-footer>
					<div></div>
				</el-footer>
				<el-form class="demo-ruleForm">
					<el-input id="c_url" class="c_url" :id="url_id" v-model="url_id" value=""></el-input>
					<el-button class="copy" type="primary" data-clipboard-target="#c_url" @click="copy" plain></el-button>
				</el-form>
			</div>
			<div id="br"></div>
			<div class="sub-title">
				<el-footer>
					<div></div>
				</el-footer>
				<el-image id="url_img" style="width: 50%; height: 50%;" :src="url_img"></el-image>
				<el-button type="primary" @click="preserve()" plain></el-button>
			</div>
			<div id="br"></div>
			<el-footer>
				<div></div>
			</el-footer>
			<div id="table">
				<el-table :data="tableData1" border>
					<el-table-column prop="todaySum" label="" width="79" align="center"></el-table-column>
					<el-table-column prop="weekSum" label="" width="79" align="center"></el-table-column>
					<el-table-column prop="monthSum" label="" width="79" align="center"></el-table-column>
					<el-table-column prop="allSum" label="" width="79" align="center"></el-table-column>
				</el-table>
				<el-table :data="tableData" border>
					<el-table-column prop="childrenNumber" label="" width="79" align="center"></el-table-column>
				</el-table>
			</div>
			<div id="br"></div>
			<el-footer>
				<div></div>
			</el-footer>
			<div style="display: flex;">
				<div class="rediv" @click="retl" :style="elMain1"></div>
				<div class="rediv" @click="rewl" :style="elMain2"></div>
				<div class="rediv" @click="reml" :style="elMain3"></div>
				<div class="rediv" @click="real" :style="elMain4"></div>
			</div>
			<el-table :data="tableData10[0].todayList" border stripe style="width: 100%" v-if="ret">
				<el-table-column type="index" label="" width="180"></el-table-column>
				<el-table-column prop="nickname" label="" width="180"></el-table-column>
				<el-table-column prop="totalmoney" label="" width="180"></el-table-column>
			</el-table>
			<el-table :data="tableData10[0].weekList" border stripe style="width: 100%" v-if="rew">
				<el-table-column type="index" label="" width="180"></el-table-column>
				<el-table-column prop="nickname" label="" width="180"></el-table-column>
				<el-table-column prop="totalmoney" label="" width="180"></el-table-column>
			</el-table>
			<el-table :data="tableData10[0].monthList" border stripe style="width: 100%" v-if="rem">
				<el-table-column type="index" label="" width="180"></el-table-column>
				<el-table-column prop="nickname" label="" width="180"></el-table-column>
				<el-table-column prop="totalmoney" label="" width="180"></el-table-column>
			</el-table>
			<el-table :data="tableData10[0].allList" border stripe style="width: 100%" v-if="rea">
				<el-table-column type="index" label="" width="180"></el-table-column>
				<el-table-column prop="nickname" label="" width="180"></el-table-column>
				<el-table-column prop="totalmoney" label="" width="180"></el-table-column>
			</el-table>
		</el-container>
	</div>
</template>

<script>
	import QRCode from 'qrcode';
	import Clipboard from 'clipboard';
	import VueQr from 'vue-qr'
	export default {
		components:{
		   VueQr
		},
		data() {
			return {
				activeIndex2: '5',
				url_img: '',
				tableData: [],
				tableData1: [],
				tableData10: [],
				url_id: "",
				ret: true,
				rew: false,
				rem: false,
				rea: false,
				elMain1: {
					color: "darkgoldenrod"
				},
				elMain2: {
					color: "white"
				},
				elMain3: {
					color: "white"
				},
				elMain4: {
					color: "white"
				}
			};
		},
		created() {
			let wt = plus.nativeUI.showWaiting();
			let url = this.$http + "/getMyInfo";
			let url1 = this.$http + "/getPersonStat";
			let url10 = this.$http + "/getTotalStat";
			let _token = localStorage.getItem("token");
			let urler = this.$http + "/getQrCode";
			this.$axios.get(urler, {
				params: {token: _token}
			}).then((res) => {
				plus.nativeUI.closeWaiting();
				this.url_img = res.data.data;
				let res_token = res.data.token;
				localStorage.setItem("token", res_token);
				let a = String(res.data.data);
				let urlid = a.substring(a.indexOf("uploadfiles/") + 12, a.indexOf(".png"));
				localStorage.setItem("_urlid", urlid);
			}).catch((err) => {
				console.log("" + err);
			})
			this.$axios.get(url1, {
				params:{token: _token}
			}).then((resp) => {
				this.tableData1.push(resp.data.data);
			}).catch((err) => {
				console.log("" + err);
			})
			this.$axios.get(url10, {
				params:{token: _token}
			}).then((resp10) => {
				this.tableData10.push(resp10.data.data);
			}).catch((err) => {
				console.log("" + err);
			})
			this.$axios.get(url, {
				params:{token: _token}
			}).then((resp) => {
				this.tableData.push(resp.data.data);
				this.url_id = "https://www.tuijianb.com/web/invitePage?code=" + localStorage.getItem("_urlid");
			}).catch((err) => {
				console.log("" + err);
			})
		},
		methods: {
			handleSelect(key, keyPath) {
				switch (parseInt(key)) {
					case 5:
						this.$refs.name2.prop = "todayList";
						break;
					case 6:
						this.$refs.name2.prop = "weekList";
						break;
					case 7:
						this.$refs.name2.prop = "monthList";
						break;
					case 8:
						this.$refs.name2.prop = "allList";
						break;
					default:
						console.log(5);
						break;
				}
			},

			//
			copy() {
				var clipboard = new Clipboard(".copy");
				clipboard.on('success', function(e) {
					e.clearSelection();
				});
				this.$notify({
					title: '',
					message: '',
					type: 'success'
				});
				clipboard.on('error', function(e) {
					console.error('Action:', e.action);
					console.error('Trigger:', e.trigger);
				});
			},
			
			//
			preserve(){
				let a = document.createElement('a');
				let event = new MouseEvent('click');
				// 
				a.download = '';
				//url 
				a.href = this.url_img;
				// 
				// a.dispatchEvent(event);
				
				// 
				var dtask = plus.downloader.createDownload(this.url_img, {}, function (d, status) {
				// 
				if (status == 200) {
					plus.gallery.save(d.filename, function () {//
						alert("");
					}, function () {
						alert("");
					});
					} else {
						alert("");
					}
				});
				dtask.start();
			},
			retl(){
				this.ret = true;
				this.rew = false;
				this.rem = false;
				this.rea = false;
				this.elMain1.color = "darkgoldenrod";
				this.elMain2.color = "white";
				this.elMain3.color = "white";
				this.elMain4.color = "white";
			},
			rewl(){
				this.ret = false;
				this.rew = true;
				this.rem = false;
				this.rea = false;
				this.elMain2.color = "darkgoldenrod";
				this.elMain1.color = "white";
				this.elMain3.color = "white";
				this.elMain4.color = "white";
			},
			reml(){
				this.ret = false;
				this.rew = false;
				this.rem = true;
				this.rea = false;
				this.elMain3.color = "darkgoldenrod";
				this.elMain1.color = "white";
				this.elMain2.color = "white";
				this.elMain4.color = "white";
			},
			real(){
				this.ret = false;
				this.rew = false;
				this.rem = false;
				this.rea = true;
				this.elMain4.color = "darkgoldenrod";
				this.elMain1.color = "white";
				this.elMain2.color = "white";
				this.elMain3.color = "white";
			},
		}
	}
</script>

<style lang="scss" scoped>
	#recommend {
		position: absolute;
		right: 0;
		top: 0;
		bottom: 50px;
		left: 0;
		overflow-y: scroll;
		
		.el-header {
			background-color: DeepSkyBlue;
			color: white;
			text-align: center;
			line-height: 60px;
		}

		.sub-title {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;

			.demo-ruleForm {
				position: relative;

				.c_url{
					width: 90%;
				}
				
				.copy {
					position: absolute;
				}
			}
		}

		#br {
			background: linear-gradient(to left, #67C23A, #409EFF, #E6A23C);
			height: 1px;
			margin-top: 20px;
		}

		.el-footer {
			text-align: center;
			line-height: 60px;
			display: flex;
			justify-content: center;
			align-items: center;

			div {
				width: 160px;
				height: 40px;
				line-height: 40px;
				text-align: center;
				background: darkgoldenrod;
				border-radius: 10px;
				color: white;
			}
		}
		#table{
			width: 317px;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			margin: 0 auto;
		}
	}
	.rediv{
		flex: 1;
		background: #808080;
		height: 50px;
		display: flex;
		align-items: center;
		justify-content: center;
	}
</style>
